  }
}

body {
  font-family: 'Quicksand', 'sans-serif';
  margin: 0;
}

.navbar {
  display: flex;
  justify-content: space-between;
  align-items: flex-end;
  border-bottom: 1px solid black;
  padding: 10px 10px 0 10px;
  font-weight: 300;
}

.navbar ul {
  align-items: flex-end;
  display: flex;
  margin: 0;
  padding: 0;
}

.navbar ul.left {
  justify-content: flex-start;
}

.navbar ul.right {
  justify-content: flex-end;
}

.navbar h1 {
  font-size: 28px;
  margin: 0;
}

.navbar li {
  border: 1px solid black;
  border-bottom: 0;
  padding: 12px;
  list-style-type: none;
  margin: 0;
  font-size: 20px;
  border-top-left-radius: 7px;
  border-top-right-radius: 7px;
}

.navbar li:hover {
  background-color: gainsboro;
}

.navbar li:active {
  background-color: darkgrey;
}

.navbar a {
  align-items: flex-end;
  color: inherit;
  text-decoration: none;
}

.app-intro {
  padding: 0 10px;
  border: 1px solid black;
  height: 100px;
}

.app-page {
  padding: 0 10px;
  border: 1px solid black;
}

.TokenInput {
  display: flex
}

.TokenInput-dropdown {
  position: relative;
  width: 100%;
}

.TokenInput-dropdown-content {
  background-color: #f1f1f1;
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
  display: block;
  position: absolute;
  width: 100%;
  z-index: 1;
}

/* Links inside the dropdown */
.TokenInput-dropdown-content span {
  border: lightgray 1px solid;
  border-collapse: collapse;
  border-radius: 2px;
  color: black;
  cursor: pointer;
  display: block;
  padding: 6px 12px;
  text-decoration: none;
}

.TokenInput-dropdown-content span:active {
  background-color: #e2e2e2;
}

/* Show the dropdown menu on hover */
.TokenInput-dropdown:hover .TokenInput-dropdown-content {display: block;}

.TokenInput-input {
  border: 0;
  font-size: inherit;
  height: inherit;
  padding: 0;
  width: 100%;
}

.TokenInput-token {
  background-color: lightblue;
  border-radius: 2px;
  margin: 0 5px;
  padding: 1px 5px 1px 5px;
}

.TokenInput-token:first-child {
  margin-left: -5px;
}

